.bubblingG {
  text-align: center;
  width:72px;
  height:45px;
  margin: auto;
}

.bubblingG span {
  display: inline-block;
  vertical-align: middle;
  width: 9px;
  height: 9px;
  margin: 22px auto;
  background: rgba(214,34,106,0.98);
  border-radius: 45px;
  -o-border-radius: 45px;
  -ms-border-radius: 45px;
  -webkit-border-radius: 45px;
  -moz-border-radius: 45px;
  animation: bubblingG 0.715s infinite alternate;
  -o-animation: bubblingG 0.715s infinite alternate;
  -ms-animation: bubblingG 0.715s infinite alternate;
  -webkit-animation: bubblingG 0.715s infinite alternate;
  -moz-animation: bubblingG 0.715s infinite alternate;
}

#bubblingG_1 {
  animation-delay: 0s;
  -o-animation-delay: 0s;
  -ms-animation-delay: 0s;
  -webkit-animation-delay: 0s;
  -moz-animation-delay: 0s;
}

#bubblingG_2 {
  animation-delay: 0.2095s;
  -o-animation-delay: 0.2095s;
  -ms-animation-delay: 0.2095s;
  -webkit-animation-delay: 0.2095s;
  -moz-animation-delay: 0.2095s;
}

#bubblingG_3 {
  animation-delay: 0.429s;
  -o-animation-delay: 0.429s;
  -ms-animation-delay: 0.429s;
  -webkit-animation-delay: 0.429s;
  -moz-animation-delay: 0.429s;
}



@keyframes bubblingG {
  0% {
    width: 9px;
    height: 9px;
    background-color:rgb(37,113,194);
    transform: translateY(0);
  }

  100% {
    width: 22px;
    height: 22px;
    background-color:rgb(255,255,255);
    transform: translateY(-19px);
  }
}

@-o-keyframes bubblingG {
  0% {
    width: 9px;
    height: 9px;
    background-color:rgb(37,113,194);
    -o-transform: translateY(0);
  }

  100% {
    width: 22px;
    height: 22px;
    background-color:rgb(255,255,255);
    -o-transform: translateY(-19px);
  }
}

@-ms-keyframes bubblingG {
  0% {
    width: 9px;
    height: 9px;
    background-color:rgb(37,113,194);
    -ms-transform: translateY(0);
  }

  100% {
    width: 22px;
    height: 22px;
    background-color:rgb(255,255,255);
    -ms-transform: translateY(-19px);
  }
}

@-webkit-keyframes bubblingG {
  0% {
    width: 9px;
    height: 9px;
    background-color:rgb(37,113,194);
    -webkit-transform: translateY(0);
  }

  100% {
    width: 22px;
    height: 22px;
    background-color:rgb(255,255,255);
    -webkit-transform: translateY(-19px);
  }
}

@-moz-keyframes bubblingG {
  0% {
    width: 9px;
    height: 9px;
    background-color:rgb(37,113,194);
    -moz-transform: translateY(0);
  }

  100% {
    width: 22px;
    height: 22px;
    background-color:rgb(255,255,255);
    -moz-transform: translateY(-19px);
  }
}