// https://uigradients.com/#GradeGrey

$mainColor: #d3a86d;
$lightenMainColor: lighten($mainColor, 5%);

$mainHeaderColor: #d5a86c;
$buttonBorderRadius: 16px;

$offsetUnit: 10px;
$halfOffsetUnit: $offsetUnit / 2;
$doubleOffsetUnit: $offsetUnit * 2;

.pull-left {
  float: left;
}

.pull-right {
  float: right;
}

.page {
  .carousel .control-dots {
    text-align: right;
    padding-right: 10px;
  }
}

.block {
  margin: $offsetUnit 0;
  overflow: hidden;

  background-color: white;
  border-radius: 2px;
  -webkit-font-smoothing: antialiased;

  font-weight: 400;

  box-sizing: border-box;

  display: block;

  padding: $offsetUnit $offsetUnit $doubleOffsetUnit;

  word-wrap: break-word;

  .blockTitle {
    margin: 0 $halfOffsetUnit $offsetUnit;
    font-size: 16px;
    font-weight: bold;
  }

  .subBlockTitle {
    background: #eaebec;

    padding: $offsetUnit;

    vertical-align: center;
    font-size: 14px;

    margin: 0 -20px;

    .withBullet:before {
      display: inline-block;
      content: "â€¢";
      font-size: 20px;
      color: $mainColor;

      margin: 0 $offsetUnit 0;
    }

    .button {
      margin-right: $offsetUnit;
      font-size: 12px;
    }

    .button, .button--material {
      line-height: 26px;
    }
  }

  &.noOffset {
    margin: 0;
    padding: 0;
  }

  &.noMargin {
    margin: 0;
  }

  &.halfMarginBottom {
    margin-bottom: $halfOffsetUnit;
  }

  &.oneMarginBottom {
    margin-bottom: $offsetUnit;
  }

  &.halfHorizontalMargin {
    margin-left: $halfOffsetUnit;
    margin-right: $halfOffsetUnit;
  }

  &.halfVerticalMargin {
    margin-top: $halfOffsetUnit;
    margin-bottom: $halfOffsetUnit;
  }

  &.oneHorizontalMargin {
    margin-left: $offsetUnit;
    margin-right: $offsetUnit;
  }

  &.oneVerticalMargin {
    margin-top: $offsetUnit;
    margin-bottom: $offsetUnit;
  }

  &.doubleHorizontalMargin {
    margin-left: $doubleOffsetUnit;
    margin-right: $doubleOffsetUnit;
  }

  &.doubleVerticalMargin {
    margin-top: $doubleOffsetUnit;
    margin-bottom: $doubleOffsetUnit;
  }


  &.doubleMarginBottom {
    margin-bottom: $doubleOffsetUnit;
  }

  &.halfMarginTop {
    margin-top: $offsetUnit;
  }

  &.oneMarginTop {
    margin-top: $offsetUnit;
  }

  &.doubleMarginTop {
    margin-top: $doubleOffsetUnit;
  }

  &.noPadding {
    padding: 0;
  }

  &.halfTopPadding {
    padding-top: $halfOffsetUnit;
  }

  &.oneTopPadding {
    padding-top: $offsetUnit;
  }

  &.doubleTopPadding {
    padding-top: $doubleOffsetUnit;
  }

  &.halfBottomPadding {
    padding-bottom: $halfOffsetUnit;
  }

  &.oneBottomPadding {
    padding-bottom: $offsetUnit;
  }

  &.doubleBottomPadding {
    padding-bottom: $doubleOffsetUnit;
  }

  &.halfVerticalPadding {
    padding-top: $halfOffsetUnit;
    padding-bottom: $halfOffsetUnit;
  }

  &.oneVerticalPadding {
    padding-top: $offsetUnit;
    padding-bottom: $offsetUnit;
  }

  &.doubleVerticalPadding {
    padding-top: $doubleOffsetUnit;
    padding-bottom: $doubleOffsetUnit;
  }

  &.halfHorizontalPadding {
    padding-left: $offsetUnit;
    padding-right: $offsetUnit;
  }

  &.oneHorizontalPadding {
    padding-left: $halfOffsetUnit;
    padding-right: $halfOffsetUnit;
  }

  &.doubleHorizontalPadding {
    padding-left: $doubleOffsetUnit;
    padding-right: $doubleOffsetUnit;
  }

  &.transparent {
    background-color: transparent;
  }

  &.halfOffset {
    margin: 5px 0;
  }

}

.fullWidth {
  width: 100%;
}

.fullHeight {
  height: 100%;
}

.textLeft {
  text-align: left;
}

.textRight {
  text-align: left;
}

.textCenter {
  text-align: center;
}

.flexContainer {
  display: flex;
  width: 100%;
  flex-direction: row;
  justify-content: space-evenly;

  align-items: stretch;

  &.spaceBetween {
    justify-content: space-between;
  }

  &.flexStart {
    justify-content: flex-start;
  }

  &.flexEnd {
    justify-content: flex-end;
  }

}

.flexCenter {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: center;
}


.withBoxShadow {
  box-shadow: 2px 2px 10px #ebf0fd;
  border-radius: $halfOffsetUnit;
}

.line {

}

.splitterWrapper {
  padding: 5px 0;

  &.horizontal {
    padding: 0 5px;
  }
}

.splitter {
  width: 1px;
  background: #e4e3e4;
  height: 100%;

  &.horizontal {
    height: 1px;
    width: 100%;
  }

}

.buttonGroup {
  border-radius: $buttonBorderRadius;
  overflow: hidden;

  display: flex;

  background: $mainColor;

  .button {
    border-radius: 0;
    width: 110px;
    text-align: center;
  }

  .splitterWrapper {
    background: transparent;
  }
}

.inputGroup {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: stretch;



  ons-input {
    width: 100%;
    border: #e0e0e0 1px solid;
    border-top-left-radius: $halfOffsetUnit;
    border-bottom-left-radius: $halfOffsetUnit;

    input {
      height: 100%;
    }
  }

  .button {
    border-radius: 0 $halfOffsetUnit $halfOffsetUnit 0;

    &.button--material{
      border-radius: 0;
    }
  }

}

ons-input[modifier='material'] {
  border: none;

  input {
    height: 100%;
  }
}
.text-input--material:focus {

  background-image: linear-gradient($mainColor, $mainColor),
  linear-gradient(to top, transparent 1px, #afafaf 1px);

  animation: material-text-input-animate 0.3s forwards;
}

.button, .button--material {
  font-size: 14px;
  background: $mainColor;
  min-height: unset;
  box-shadow: unset;

  border-radius: $buttonBorderRadius;

  padding: 0 18px;

  &:active {
    background: $lightenMainColor;
  }
}

.button.small {
  line-height: 20px;
  font-size: 12px;
}

.toolbar {
  background: $mainHeaderColor;

  $textColor: white;

  .back-button__icon {
    fill: $textColor;
  }

  .toolbar__title, .back-button {
    color: $textColor;
  }
}

.mainBackground {
  background-color: #d3a86d;
}

.textMainColor {
  color: $mainColor;
}

.textBolder {
  font-weight: bolder;
}

// begin business related

:checked + .tabbar__button {
  color: $mainColor;
}

.tabbar--material__border {
  background-color: $mainColor;
}

.myAssertCard {

  .splitter {
    background: white;
  }

  .myAssetBlockLabel {
    color: white;
    width: 120px;
    background: $mainColor;
    padding: $offsetUnit $doubleOffsetUnit;

    $borderRadius: 8px;
    border-bottom-left-radius: $borderRadius;
    border-bottom-right-radius: $borderRadius;
  }

  .myAssetBlockNumber {
    font-size: 18px;
  }
}

.accountText {
  margin-left: $offsetUnit;
  display: inline-block;
  font-size: 12px;
  font-weight: normal;
}

.gameCard {

  .flexContainer > div {
    padding: $offsetUnit $offsetUnit 0 $offsetUnit;
  }

  img {
    width: 100%;
  }
}

.myTaskCard {
  $height: 36px;
}

.bottomLine {
  margin: $doubleOffsetUnit auto;
  width: 295px;
  height: 10px;
  background-size: contain;
  background-image: url("./app/home/bottomLine@3x.png");
  background-repeat: no-repeat;
  background-position: center;
}

.rankTabWrapper {
  a {
    color: #000;
    text-decoration: none;
  }

  .isActive {
    border-bottom: 1px red solid;
    a {
      color: red;
    }
  }

  .rankTabItem {
    height: 100%;
  }
}

